
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
#header {
  position: fixed;
  width: 100%;
  z-index: 99;
  top: 0;
}
#footer-nav a {
  text-indent: -9999px;
  background: url("/static/img/btn_menu.png") no-repeat;
  background-size: auto 100%;
  width: 22px;
}
#footer-nav .shoppingCart {
  width: 35px;
}
#header .top-icon {
  width: 20%;
}
#header .main-logo {
  width: 60%;
}
#header .user {
  width: 20%;
  display: flex;
  justify-content: flex-end;
  align-content: center;
  margin-right: 2rem;
}
#header .cart-link {
  white-space: nowrap;
}
#header .count {
  display: inline-block;
  line-height: 5rem;
  color: #fff;
  font-weight: bold;
  margin-left: -0.3rem;
}
#header .main-logo img {
  height: 3rem;
  margin-top: 1rem;
  vertical-align: top;
}
#header .user img {
  height: 2.5rem;
  margin-top: 1rem;
  vertical-align: top;
}
#header .user > img:first-child {
  margin-right: 1rem;
}
#header .top-icon .icon-bar > span:first-child {
  margin-top: 1.8rem;
  margin-bottom: 1rem;
}
#header .top-icon .icon-bar > span {
  display: block;
  width: 2rem;
  height: 0.2rem;
  background: #fff;
  margin-left: 2rem;
}
#header .top-icon .nav-back {
  background: url(/static/img/nav_back.png) no-repeat;
  background-size: contain ;
  width: 20px;
  height: 20px;
  display: inline-block;
  margin-top: 15px;
}



